.basket {
    width: 22px;
    height: 20px;
    margin-left: 62px;
    display: flex;
    position: relative;
    cursor: pointer;
}

.burger {
    display: none;
}

.burger > img, .cross > img {
    display: none;
}

.basket--counter {
    position: absolute;
    background-color: #ED1C24;
    border: 3.7px solid #FFFFFF;
    font-weight: 600;
    font-size: 13px;
    line-height: 13px;
    border-radius: 50%;
    font-family: 'Graphik LCG';
    color: #fff;
    padding: 3px 5px;
    top: -13px;
    margin-left: 13px;
}

.basket--counter--empty {
    display: none;
}

.basket img {
    width: 100%;
    height: 100%;
}

.navigation__catalog_block {
    position: absolute;
    top: 87px;
    left: 0;
    margin: 0;
    background-color: #F5F5F5;
    position: absolute;
    width: 100%;
    visibility: hidden;
    opacity: 0;
    transition: .3s ease-in-out;
    z-index: 9;
}

.navigation__catalog_block:before {
    content: " ";
    width: 100%;
    height: 36px;
    position: absolute;
    top: -36px;
}

.navigation__catalog_block__open {
    visibility: visible;
    opacity: 1;
}

.navigation__catalog_block > div {
    margin: 0 7vw;
    padding: 25px 0;
    display: flex;
    justify-content: space-between;
}

.navigation__catalog_block a {
    font-size: 16px;
    line-height: 15px;
}

.time_work {
    display: none;
    font-size: 14px;
    line-height: 13px;
    font-weight: bold;
    color: #ED1C24;
    margin: 100px 0 40px;
}

.time_work span {
    color: #282A32;
    font-size: 16px;
    line-height: 15px;
}

.main_page {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 70vh;
    display: flex;
    align-items: center;
}

.main_page__block {
    margin-top: 84px;
    display: flex;
    flex-direction: column;
    color: #fff;
    width: 100%;
}

.main_page__block > p {
    font-family: 'Graphik LCG';
    font-weight: 500;
    font-size: 18px;
    line-height: 23px;
    color: #FFF;
    margin-top: 20px;
    max-width: 480px;
}

h1 {
    font-family: 'Graphik LCG';
    font-weight: bold;
    font-size: 64px;
    line-height: 64px;
    color: #FFF;
}

.main_page__block > div {
    margin-top: 40px;
    display: flex;
}

.button__catalog {
    font-family: 'Graphik LCG';
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    padding: 18px 27px;
    background-color: #ED1C24;
    border-radius: 3px;
    color: #FFF;
}

.about_block {
    display: flex;
    flex-wrap: wrap;
}

.about_block > div {
    max-width: 50%;
    width: 100%
}

h2 {
    font-family: 'Gotham Pro';
    font-weight: normal;
    font-size: 32px;
    line-height: 31px;
    width: 100%;
    margin-bottom: 40px;
}

.about_block__text > p {
    font-family: 'Gotham Pro';
    font-weight: normal;
    font-size: 18px;
    line-height: 32px;
    margin-right: 30px;
}

.about_block img {
    width: 100%;
}

.catalogSmall, .catalog {
    background-color: #F5F5F5;
}

.catalogSmall__items {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0 -15px;
}

.catalog_block {
    display: flex;
    flex-direction: column;
}

.catalog__items {
    display: grid;
    column-gap: 20px;
    row-gap: 20px;
    grid-template-columns: repeat(4, 1fr);
}

.catalog__items > .items {
    max-width: none;
    margin: 0;
}

.items {
    max-width: calc(25% - 30px);
    margin: 0 15px 30px;
    background-color: #fff;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.items__img {
    height: 100%;
}

.items__img > img{
    width: 100%;
    height: 100%;
}

.items__data {
    padding: 25px;
    min-height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.items__data > p {
    font-size: 16px;
    line-height: 18px;
}

.price {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.price > h3{
    font-size: 20px;
    font-weight: 600;
    line-height: 19px;
}

.basket__add {
    padding: 7px 15px;
    width: max-content;
    height: max-content;
    display: flex;
    transition: 0.3s ease-in-out;
    border: none;
    background-color: unset;
}

.basket__add:hover {
    background-color: #ED1C24;
    border-radius: 2.7px;
    transition: 0.3s ease-in-out;
}

.basket__add:hover > img {
    filter: grayscale(1) contrast(2) invert(1);
    transition: 0.3s ease-in-out;
}

.advantage__items {
    display: flex;
    justify-content: space-between;
    margin: 0 -15px;
    flex-wrap: wrap;
}

.item_ang {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 0 15px;
    width: calc(25% - 30px);
}

.item_ang > img {
    width: 64px;
    height: 64px;
    margin-bottom: 25px;
}

.contact {
    background-color: #F5F5F5;
}

.contact_block {
    display: flex;
    flex-wrap: wrap;
}

.contact_block > div {
    display: flex;
    flex-wrap: nowrap;
    width: 100%;
}

.contact_item {
    max-width: 35%;
}

.contact_item > div {
    background-color: #fff;
    padding: 55px 34px;
    display: flex;
    flex-direction: column;

}

h4 {
    color: #ED1C24;
    font-size: 14px;
    font-weight: bold;
    line-height: 13px;
    margin-bottom: 10px;
}

.contact_item a {
    font-size: 16px;
    line-height: 15px;
    margin-bottom: 35px;
}

#map {
    min-width: 65%;
    height: 56vh;
    margin-left: 30px;
    width: 100%;
    display: flex;
}

#map img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.footer {
    background-color: #F5F5F5;
}

.footer_block {
    display: flex;
    justify-content: space-between;
    padding: 45px 0;
    border-top: 1px solid #40404020;
    align-items: center;
}

.logo > p {
    margin-top: 10px;
    font-size: 10px;
    line-height: 10px;
    display: flex;
    align-items: center;
    text-align: center;
}

.footer_contact p {
    font-size: 16px;
    line-height: 15px;
}

.studio {
    color: #ED1C24;
    font-size: 16px;
    line-height: 15px;
    font-weight: bold;
    position: relative;
}

.studio:after {
    background: none repeat scroll 0 0 transparent;
    bottom: -4px;
    content: "";
    display: block;
    height: 2px;
    left: 50%;
    position: absolute;
    background: #ED1C24;
    -webkit-transition: width 0.3s ease 0s, left 0.3s ease 0s;
    -o-transition: width 0.3s ease 0s, left 0.3s ease 0s;
    transition: width 0.3s ease 0s, left 0.3s ease 0s;
    width: 0;
}

.studio:hover:after {
    width: 100%;
    left: 0;
}

.ReactModal__Overlay {
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 20;
    display: flex;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s ease-in-out;
    overflow: auto;
    background-color: #181818db;
}

.modal_basket {
    display: flex;
    margin: auto;
    z-index: 20;
    flex-direction: column;
    padding: 10px 10px;
}

.modal_basket__cross {
    display: flex;
    justify-content: flex-end;
}

.modal_basket__cross > img {
    cursor: pointer;
}

.modal_basket__block {
    display: flex;
    background-color: #fff;
    border-radius: 3px;
    padding: 44px;
    flex-direction: column;
    flex-wrap: wrap;
    margin: 5px 27px;
}

.modal_basket__block > h2 {
    margin-bottom: 5px;
}

.order__summ {
    margin: 5px 0 35px;
}

.order__summ * {
    font-size: 20px;
    line-height: 32px;
}

.order__summ span {
    margin-left: 10px;
    color: #ED1C24;
    font-weight: bold;
}

.basket--empty .basket--empty_label, .basket--empty .order--empty {
    display: flex;
    visibility: visible;
    opacity: 1;
}

.order--empty,
.basket--empty_label,
.basket--empty .basket__content, 
.basket--empty .order__summ,
.basket--empty .order__submit {
    display: none;
    visibility: hidden;
    opacity: 0;
}

.modal_basket__block form {
    width: 100%;
}

.basket__content  {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.basket__content_item {
    display: grid;
    padding: 30px 0;
    width: 100%;
    grid-template-columns: 4fr 8fr 1fr 1fr;
    height: auto;
}

.basket__content_item:not(:first-child) {
    border-top: 1px solid #DDDCDC;
}

.basket__content_item__image {
    max-height: 87px;
}


.basket__content_item__image img {
    width: 100%;
    object-fit: cover;
    height: 100%;
}

.basket__content_item > .price {
    display: flex;
    flex-direction: column;
    align-content: space-between;
    align-items: flex-start;
    margin: 0 15px 0 10px;
}

.basket__content_item > .count {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 21.5px;
    margin: 10px 0;
}

.basket__content_item > .count button {
    border: none;
    background-color: unset;
    width: 15px;
    height: 15px;
    transform: rotate(-46deg);
}

.basket__content_item > .count .count--up {
    border-top: 2px solid #000;
    border-right: 2px solid #000;
}

.basket__content_item > .count .count--down {
    border-bottom: 2px solid #000;
    border-left: 2px solid #000;
}

.trash_box {
    display: flex;
    align-items: center;
    padding: 10px;
    margin: auto 0;
}

.trash_box > img {
    cursor: pointer;
}

.basket__content--empty ~ .order__summ {
    display: none;
    visibility: hidden;
    opacity: 0;
}

